import React from "react";

import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';

import createSvgIcon from "@material-ui/icons/utils/createSvgIcon";
import {withStyles} from "@material-ui/core/styles";

const noTransformText: React.CSSProperties = {
  textTransform: "none"
};

const styles = {
  shareButton: {
    ...noTransformText,
    background: "rgb(29, 161, 242)",
    padding: "10px 25px",
    marginTop: "16px",
    borderRadius: "48px",
    border: "none",
    fontSize: "0.9rem",
    fontWeight: 700,
    color: "#fff",
    alignSelf: "center",
    fontFamily: "Montserrat, sans-serif",
    "&:hover": {
      background: "rgb(49, 181, 255)"
    }
  },
  icon: {
    color: "#FFF"
  },
  justIcon: {
    color: "rgb(29, 161, 242)"
  }
};

type ShareInTwitterButtonProps = {
  text: string;
  hashtag?: string,
  label?: string;
  justIcon?: boolean,
  url?: string,
  classes?: any,
}


const TwitterIcon = createSvgIcon(
  <path
    d="M23.643 4.937c-.835.37-1.732.62-2.675.733.962-.576 1.7-1.49 2.048-2.578-.9.534-1.897.922-2.958 1.13-.85-.904-2.06-1.47-3.4-1.47-2.572 0-4.658 2.086-4.658 4.66 0 .364.042.718.12 1.06-3.873-.195-7.304-2.05-9.602-4.868-.4.69-.63 1.49-.63 2.342 0 1.616.823 3.043 2.072 3.878-.764-.025-1.482-.234-2.11-.583v.06c0 2.257 1.605 4.14 3.737 4.568-.392.106-.803.162-1.227.162-.3 0-.593-.028-.877-.082.593 1.85 2.313 3.198 4.352 3.234-1.595 1.25-3.604 1.995-5.786 1.995-.376 0-.747-.022-1.112-.065 2.062 1.323 4.51 2.093 7.14 2.093 8.57 0 13.255-7.098 13.255-13.254 0-.2-.005-.402-.014-.602.91-.658 1.7-1.477 2.323-2.41z"/>,
  "TwitterIcon"
);


const ShareInTwitterButton: React.FC<ShareInTwitterButtonProps> = ({
                                                                     text,
                                                                     label = "Tweet",
                                                                     justIcon = false,
                                                                     hashtag,
                                                                     url,
                                                                     classes
                                                                   }) => {
  const encodedMessage = encodeURI(text);
  const hashPart = hashtag ? `button_hashtag=${hashtag}` : "";
  const urlPart = url ? `&url=${encodeURI(url)}`: "";
  const tweetUrl = `https://twitter.com/intent/tweet?text=${encodedMessage}&${hashPart}${urlPart}`;

  const shareButton = justIcon ?(
    <IconButton>
      <TwitterIcon className={classes.justIcon}/>
    </IconButton>
  ) :(<Button
    className = {classes.shareButton}
    startIcon={<TwitterIcon className={classes.icon}/>}
    href={tweetUrl}>{label}</Button>);
  return shareButton;
}

export default withStyles(styles)(ShareInTwitterButton);
